import React from 'react'
import {observer} from 'mobx-react-lite'
import './NewsCard.scss'
import {faCalendar, faClock} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

interface Interface {
    data:any
}
export default observer(({data}:Interface) => {

    return (
        <div className="card card-compact w-96 bg-base-100 shadow-xl card-news">
            <figure><img src="https://hsto.org/r/w1560/getpro/habr/upload_files/f81/1dc/7ed/f811dc7ed07583d61d2456c895202f0a.png" alt="Shoes" /></figure>
            <div className="card-body">
                <h2 className="card-title">{data.title}</h2>
               <div className={'card-news-metadata'}>
                   <div className="badge badge-outline card-news-metadata-item"><FontAwesomeIcon icon={faClock} className={'icons'}/> { data.time}</div>
                   <div className="badge badge-outline card-news-metadata-item"><FontAwesomeIcon icon={faCalendar} className={'icons'}/> { data.date}</div>
               </div>
                <p className={'card-news-descr'}>{data.shortDescr}</p>
                <div className="card-actions justify-end">
                    <button className="btn btn-primary">Buy Now</button>
                </div>
            </div>
        </div>
    )
})
