import React from 'react'
import {observer} from 'mobx-react-lite'
import './SearchForm.scss'
import loansStateStore from "../../../lib/store/loans-state-store";
import LoansCard from "../../../general/Cards/LoansCard/LoansCard";
import {nanoid} from "nanoid";

export default observer(() => {

    const filterVal = loansStateStore.dataLoan.filter((val) => {
        return val.title.toLowerCase().includes(loansStateStore.search.toLowerCase())
    })

    return (
        <div className={'search-form'}>
            <input value={loansStateStore.search} onChange={(e) => loansStateStore.setSearch(e.target.value)}
                   type="text" placeholder={loansStateStore.placeholderSearch} className="input bg-base-200 w-full mobile"/>
            <br/>
            <br/>
            <div className={'flex flex-wrap search-form-body'}>
                {
                    filterVal.map((data) => (
                        <LoansCard key={nanoid()} data={data}/>
                    ))
                }
            </div>
        </div>
    )
})
