import React from 'react'
import {observer} from 'mobx-react-lite'
import './LoansCard.scss'
import {DataLoan} from "../../../lib/models/dataLoan";
import {nanoid} from "nanoid";
import {passControl} from "../../../lib/helper";
import loansStateStore from "../../../lib/store/loans-state-store";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBell, faClock, faCoins, faPercent} from "@fortawesome/free-solid-svg-icons";

interface ILoansCard {
    data: DataLoan
}

export default observer(({data}: ILoansCard) => {


    return (
        data.place < 15 ?
            <div className="card  bg-base-100 shadow-xl card-loans">
                <div className="top-loans-name">
                    <div className="avatar placeholder">
                        <div className=" w-16">
                            <img alt={"13"} className={'card-loans-avatar'} src={data.img}/>
                        </div>
                    </div>
                    {data.title}
                </div>
                <div className="card-body">
                    <div>
                       <div className={'card-loans-body-container'}>
                           {
                               data.descr.map((descr) => (

                                   <div key={nanoid()}>
                                       {
                                           descr.stocks === '' ?  <div key={nanoid()} className={'loans-card-descr bg-base-200'}>
                                                   <div style={{color: '#ED3F37'}}
                                                        className={'loans-card-descr-title'}> <FontAwesomeIcon
                                                       icon={faBell} style={{color: '#185AC5'}}
                                                       className={'loans-card-descr-icon'}/> </div>   
                                               </div>
                                               :
                                               <div key={nanoid()} className={'loans-card-descr bg-base-200'}>
                                                   <div style={{color: '#ED3F37'}}
                                                        className={'loans-card-descr-title'}> <FontAwesomeIcon
                                                       icon={faBell} style={{color: '#185AC5'}}
                                                       className={'loans-card-descr-icon'}/> </div>{descr.stocks}
                                               </div>
                                       }
                                       {
                                           descr.summa === '' ? null
                                               :
                                               <div key={nanoid()} className={'loans-card-descr bg-base-200'}>
                                                   <div key={nanoid()} className={'loans-card-descr-title'}> <FontAwesomeIcon
                                                       icon={faCoins} style={{color: '#185AC5'}}
                                                       className={'loans-card-descr-icon'}/> </div>{descr.summa}
                                               </div>
                                       }
                                       {
                                           descr.percent === '' ? null
                                               :
                                               <div key={nanoid()} className={'loans-card-descr bg-base-200'}>
                                                   <div key={nanoid()} className={'loans-card-descr-title'}> <FontAwesomeIcon
                                                       icon={faPercent} style={{color: '#185AC5'}}
                                                       className={'loans-card-descr-icon'}/>
                                                   </div>{descr.percent}
                                               </div>
                                       }
                                       {
                                           descr.time === '' ? null
                                               :
                                               <div key={nanoid()} className={'loans-card-descr bg-base-200'}>
                                                   <div key={nanoid()} className={'loans-card-descr-title'}> <FontAwesomeIcon
                                                       icon={faClock} style={{color: '#185AC5'}}
                                                       className={'loans-card-descr-icon'}/> </div>{descr.time}
                                               </div>
                                       }
                                   </div>


                               ))
                           }
                       </div>
                        <br/>
                        <a href={passControl() ? null
                            :
                            data.link}
                           onClick={
                               passControl() ?
                                   null
                                   :
                                   () => {

                                       loansStateStore.setStateLoader(true)
                                   }
                           } style={{width: '100%'}} className="btn btn-primary"></a>
                        <div className={'card-loans-license'}>{data.license}</div>
                    </div>
                </div>
            </div> :
            null
    )
})




