import React from 'react'
import {observer} from 'mobx-react-lite'
import './BottomApp.scss'
import routes from "../../../app/lib/routes";
import loansStateStore from "../../../app/lib/store/loans-state-store";
import {faCircleInfo, faHome} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";


export default observer(() => {

    return (
        <div className="btm-nav">
            <a href={'#' + routes.HOME}  onClick={()=>loansStateStore.setBottomBtn("")} className={loansStateStore.bottomBtn === '' && "active"}>
                <FontAwesomeIcon icon={faHome}/>
            </a>
            <a href={'#' + routes.INFO} id={""} onClick={()=>loansStateStore.setBottomBtn("")} className={loansStateStore.bottomBtn === '' && "active"}>
                <FontAwesomeIcon icon={faCircleInfo}/>
            </a>
        </div>
    )
})
